.button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background-color: #7859cf;
  border: none;
  border-radius: 4px;
  display: inline-block;
  padding: 8px 4px;
  box-sizing: border-box;
  cursor: pointer;
}
.button:hover,
.button:focus {
  background-color: #9070ec;
}
.button:active {
  background-color: #593cac;
  color: #ffffff;
}
.button:disabled {
