  .main-footer__logo-image--tablet {
    display: block;
  }
  .main-footer__social::after,
  .main-footer__nav::after,
  .main-footer__media::after,
  .main-footer__contacts::after {
    display: none;
  }
  .main-footer__nav,
  .main-footer__media,
  .main-footer__contacts,
  .main-footer__copyright {
    padding: 0;
  }
  .main-footer__address-text br {
    display: none;
  }
  .main-footer__copyright {
    grid-column: 1 / -1;
  }
}
@media (min-width: 1440px) {
  .main-footer {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 1360px;
    padding-top: 40px;
    row-gap: 20px;
  }
  .main-footer__social {
    grid-column: auto;
  }
  .main-footer__logo-link {
    width: auto;
  }
  .main-footer__logo-link svg {
    margin-bottom: 4px;
  }
  .main-footer__logo-text {
    display: inline-block;
    margin-bottom: 50px;
  }
  .main-footer__contacts,
  .main-footer__media,
  .main-footer__nav {
    padding-top: 10px;
  }
}
.social-list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  margin-left: auto;
  padding: 0;
  list-style: none;
  min-width: 160px;
}
.social-list__item:not(:first-of-type) {
  margin-left: 20px;
}
.social-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
}
.social-list__link:hover,
.social-list__link:focus {
  background-color: #f0ebff;
  border-radius: 4px;
}
.social-list__link:hover svg,
.social-list__link:focus svg {
  fill: #7859cf;
}
.social-list__link:active {
  background-color: #7859cf40;
}
.social-list__link:active svg {
  fill: #7859cf;
}
@media (min-width: 1440px) {
  .social-list {
    margin-right: auto;
    margin-left: 0;
    justify-content: flex-start;
  }
}
.map {
  position: relative;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  overflow: hidden;
  background-color: #e2e2e2;
}
.map__canvas {
  width: 100%;
  height: 420px;
  background-color: #f8f8f8;
}
.map__canvas--nojs {
  position: relative;
  margin: 0 auto;
  width: 320px;
  background-image: url("../img/map-mobile.jpg");
}
@media (min-resolution: 2dppx) {
  .map__canvas--nojs {
    background-image: url("../img/map-mobile@2x.jpg");
  }
}
.map__canvas--nojs::after {
  content: "";
  position: absolute;
  width: 38px;
  height: 50px;
  background-image: url("../img/icon-map-pin.svg");
  background-repeat: no-repeat;
  background-size: 38px 50px;
  top: 185px;
  left: 156px;
}
@media (min-width: 768px) {
  .map__canvas {
    height: 480px;
  }
  .map__canvas--nojs {
    width: 768px;
    background-image: url("../img/map-tablet.jpg");
  }
  .map__canvas--nojs::after {
    top: 215px;
    left: 380px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .map__canvas--nojs {
    background-image: url("../img/map-tablet@2x.jpg");
  }
}
@media (min-width: 1440px) {
  .map__canvas {
    height: 540px;
  }
  .map__canvas--nojs {
    width: 1440px;
    background-image: url("../img/map-desktop.jpg");
  }
  .map__canvas--nojs::after {
    top: 245px;
    left: 716px;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .map__canvas--nojs {
    background-image: url("../img/map-desktop@2x.jpg");
  }
}
